.section-padding .col-lg-12 h5 {
    font-family: "Roboto", sans-serif;
  }
  .testimonial-two {
    background: #202020;
    border-radius: 10px;
    padding: 30px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
    max-width: 505px;
  }
  .testimonial-post {
    overflow: hidden;
    display: table;
  }
  .testimonial-post .post,
  .testimonial-post .text-content {
    display: table-cell;
  }
  .testimonial-post .post {
    height: 60px;
    width: 60px;
  }
  .testimonial-post .post > img {
    width: 100%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
  }
  .testimonial-post .text-content {
    vertical-align: middle;
    padding-left: 10px;
  }
  .testimonial-post:hover a,
  .testimonial-post:focus a {
    color: #18d26e;
  }
  .testimonial-two .testimonial-para:before {
    display: block;
    font-size: 28px;
    color: #acacac;
    margin-bottom: 15px;
    content: "\f10d";
    font-weight: 700;
    font-family: "Font Awesome 5 Free";
  }
  .main-title {
    position: relative;
    margin-bottom: 6rem;
    text-align: center;
  }
 
  .main-title h2 {
    font-weight: 300;
    color: #ffffff;
    line-height: 1.2;
    max-width: 650px;
    margin-bottom: 1.5rem;
    margin-left: auto;
    margin-right: auto;
  }
  
  
  
 
  /* .testimonial-item{
